export default {
  data: function () {
    return {
      addressLoading: false,
      addressInfo: {},
      receivedTxs: {},
      walletNotification: [],
      txBroadcasting: false
    }
  },
  mounted () {
    this.getAddressInfo()
  },
  created () {
  },
  methods: {
    loginRequired () {
      if (!this.loggedViewMode() && !this.loggedSpendMode()) {
        this.openModal('loginModal', true)
        return true
      } else if (!this.loggedSpendMode()) {
        this.openModal('pinModal', true)
        return true
      }
      return false
    },
    getLiveAddressInfo () {
      this.addressStream()
    },
    getUserPasswordHash () {
      const userPasswordHashLocalStorage = localStorage.getItem('user.userPasswordHash')
      const userPasswordHashSessionStorage = sessionStorage.getItem('user.userPasswordHash')

      if (userPasswordHashSessionStorage) {
        return userPasswordHashSessionStorage
      }
      if (userPasswordHashLocalStorage) {
        return userPasswordHashLocalStorage
      }
      return
    },
    getPin () {
      const pinLocalStorage = localStorage.getItem('user.pin')
      const pinSessionStorage = sessionStorage.getItem('user.pin')
      if (pinSessionStorage) {
        return pinSessionStorage
      }
      if (pinLocalStorage) {
        return pinLocalStorage
      }
      return
    },
    getPrivateKey () {
      const privateKeyLocalStorage = localStorage.getItem('user.privateKey')
      const privateKeySessionStorage = sessionStorage.getItem('user.privateKey')

      if (privateKeySessionStorage) {
        return privateKeySessionStorage
      }
      if (privateKeyLocalStorage) {
        return privateKeyLocalStorage
      }
      return
    },
    getUsername () {
      const usernameLocalStorage = localStorage.getItem('user.username')
      const usernameSessionStorage = sessionStorage.getItem('user.username')
      if (usernameSessionStorage) {
        return usernameSessionStorage
      }
      if (usernameLocalStorage) {
        return usernameLocalStorage
      }
      return
    },
    getAddress () {
      const addressLocalStorage = localStorage.getItem('user.address')
      const addressSessionStorage = sessionStorage.getItem('user.address')
      if (addressSessionStorage) {
        return addressSessionStorage
      }
      if (addressLocalStorage) {
        return addressLocalStorage
      }
      return
    },
    loggedViewMode () {
      if ((this.getPrivateKey() || this.getUserPasswordHash()) && !this.getPin()) {
        return true
      }
      return false
    },
    loggedSpendMode () {
      if ((this.getPrivateKey() || this.getUserPasswordHash()) && this.getPin()) {
        return true
      }
      return false
    },
    calculatePrivateKey () {
      if (!this.getPin()) {
        return
      }
      if (this.getPrivateKey()) {
        var bytes  = CryptoJS.AES.decrypt(this.getPrivateKey().toString(), this.getPin())
        return bytes.toString(CryptoJS.enc.Utf8)
      }
      if (this.getUserPasswordHash()) {
        const pinHash = bitcore.crypto.Hash.sha256(Buffer.from(this.getPin()))
        const buffArray = [Buffer.from(this.getUserPasswordHash(), 'hex'), pinHash]
        const hash = bitcore.crypto.Hash.sha256(Buffer.concat(buffArray))
        const bn = bitcore.crypto.BN.fromBuffer(hash)
        const pk = bitcore.PrivateKey(bn).toString()
        return pk
      }
      return
    },
    addressFromPrivateKey () {
      try {
        const pk = this.calculatePrivateKey().toString()
        if (bitcore.PrivateKey.isValid(pk)) {
          return bitcore.PrivateKey(pk).toAddress().toString()
        }
      } catch (err) {
        return
      }
      return
    },
    getAddressInfo () {
      if (this.getAddress() && !this.addressLoading) {
//        console.log('walletMixin.getAddressInfo')
        this.addressLoading = true
        const insight = new bitexplorer.Insight(blockExplorerRpc)
        let that = this
        insight.getUnspentUtxos(bchaddr.toLegacyAddress(this.getAddress()), function (err, res) {
          let balanceSat  = 0
          for (let i = 0; i < res.length; ++i) {
            balanceSat += res[i].satoshis
          }
          that.addressInfo = {balanceSat: balanceSat}
          that.addressLoading = false
        })
      }
    },
    addressStream () {
      if (this.getAddress()) {
        let address = this.getAddress()
        address = address.replace('bitcoincash:', '')
        let query = {
          'v': 3,
          'q': {
            'find': {
              '$or': [
                {'in.e.a': address},
                {'out.e.a': address}
              ]
            }
          }
        }
        const b64 = Base64.encode(JSON.stringify(query))
        const url = bitsocketNode + b64

        let es = new EventSource(url);
        es.addEventListener('message', event => {
          let eventData = JSON.parse(event.data)
          if (event.data !== undefined && event.data !== '{"type":"open","data":[]}' && eventData.data.length > 0) {
            this.getAddressInfo()
            this.receivedTxs = eventData
          }
        }, false)
        es.addEventListener('error', event => {
          if (event.readyState == EventSource.CLOSED) {
            console.log('Event was closed')
            console.log(EventSource)
          }
        }, false);
      }
    },
    sendTransaction (dataArray = [], toArray = []) {
      this.txBroadcasting = true
      let amountSat = 0
      for (let i = 0; i < toArray.length; ++i) {
        if (toArray[i].value < 546) {
          let walletError = 'Amount is less than the minimum dust limit (546 sat).'
          sessionStorage.setItem('walletError', walletError)
          this.txBroadcasting = false
          console.log('datacash build failed: ', walletError)
          return
        }
        amountSat += toArray[i].value
      }
      if (this.addressInfo.balanceSat < amountSat) {
        let walletError = 'Wallet has insufficient funds'
        sessionStorage.setItem('walletError', walletError)
        this.txBroadcasting = false
        console.log('datacash build failed: ', walletError)
        this.$router.push('/wallet/receive')
        return
      }

      let datacashTx = {}
      datacashTx['cash'] = {}
      if (dataArray) {
        datacashTx['data'] = dataArray
      }
      datacashTx['cash']['rpc'] = blockExplorerRpc
      datacashTx['cash']['key'] = this.calculatePrivateKey()
      if (toArray) {
        datacashTx['cash']['to'] = toArray
      }
      let that = this

      datacash.build(datacashTx, function(error, res) {
        if (error) {
          let walletError = 'datacash build failed: ' + error
          sessionStorage.setItem('walletError', walletError)
          that.txBroadcasting = false
          console.log('datacash build failed: ', walletError)
          return
        }
        if (that.addressInfo.balanceSat <= amountSat + parseInt(res._estimateSize() * 1.4)) {
          let walletError = 'Wallet has insufficient funds for fee (' + parseInt(res._estimateSize() * 1.4) + 'sat).'
          sessionStorage.setItem('walletError', walletError)
          that.txBroadcasting = false
          console.log('datacash build failed: ', walletError)
          that.$router.push('/wallet/receive')
          return
        }
        datacash.send(datacashTx, function(error, hash) {
          if (error) {
            let walletError = 'Transaction failed: ' + error
            sessionStorage.setItem('walletError', walletError)
            that.txBroadcasting = false
            console.log('datacash build failed: ', walletError)
            return
          }
          console.log('Transaction sent successfully. Transaction hash: ' + hash)
          that.txBroadcasting = false
        })
      })
    },
    logout () {
      sessionStorage.removeItem('user.userPasswordHash')
      sessionStorage.removeItem('user.pin')
      sessionStorage.removeItem('user.privateKey')
      sessionStorage.removeItem('user.username')
      sessionStorage.removeItem('user.address')
      localStorage.removeItem('user.userPasswordHash')
      localStorage.removeItem('user.pin')
      localStorage.removeItem('user.privateKey')
      localStorage.removeItem('user.username')
      localStorage.removeItem('user.address')
      location.reload()
    }
  }
}



