<template>
  <span>
        <li v-if="logged" style="float: right;"><a href="javascript:void(0)" @click="openModal('newModal')" class="button-active">Post</a></li>
     		<li v-if="logged" class="dropdown" style="float: right;">
          <i class="fas fa-user-circle profile" @click="showProfileNav = !showProfileNav"></i>
            <ul v-bind:class="{ 'nav-dropdown': !showProfileNav, 'nav-dropdown-active': showProfileNav }">
              <li><a href="javascript:void(0)">Balance: {{ balance }} sat</a></li>
              <li><router-link to="/pages/editProfile">Edit profile</router-link></li>
              <li><router-link to="/wallet/receive">Receive {{ networkName || 'BSV' }}</router-link></li>
              <li><router-link to="/wallet/send">Send {{ networkName || 'BSV' }}</router-link></li>
              <li><router-link to="/wallet/export">Export wallet key</router-link></li>
              <li><a href="/" @click="logout">Logout</a></li>
            </ul>
        </li>
     		<li v-else style="float: right;"><a href="javascript:void(0)" @click="openModal('loginModal')" class="button-active">Sign in</a></li>
  </span>
</template>


<script>
  export default {
      name: 'UserNav',
      mixins: [],
      data () {
        return {
          networkName: window.networkName,
          showProfileNav: false
        }
      },
      props: {
      },
      mounted () {
      },
      computed: {
        logged: function () {
          if (this.loggedViewMode() || this.loggedSpendMode()) {
            return true
          }
          return false
        },
        balance: function () {
          if (this.addressInfo) {
            return this.addressInfo.balanceSat
          }
          return 0
        }
      },
      methods: {
      }
  };
</script>

<style>
.profile {
  margin-top:10px;
  margin-right: 10px;
  font-size: 32px;
  text-decoration: none;
  color: #657786;
}
@media screen and (max-width: 800px) {
	 .profile {
		 display: none;
	}
  .nav-dropdown {
    display: block;
    margin: 0;
  }
  .nav-dropdown-active {
    display: block;
    margin: 0;
  }
  .button-active {
    display: block;
    margin: 0;
  }
}
</style>



