import Vue from 'vue'
import Router from 'vue-router'
import Intro from '@/components/Intro'
import Home from '@/components/Home'
import Page from '@/components/Page'
import WalletReceive from '@/components/Wallet/Receive'
import WalletSend from '@/components/Wallet/Send'
import WalletExport from '@/components/Wallet/Export'
import UserMuted from '@/components/User/Muted'
import UserPosts from '@/components/User/Posts'
import UserLikes from '@/components/User/Likes'
import UserFollowing from '@/components/User/Following'
import UserFollowers from '@/components/User/Followers'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Intro
    },
    {
      path: '/home',
      component: Home
    },
    {
      path: '/pages/:page',
      component: Page,
      props: true
    },
    {
      path: '/wallet/receive',
      component: WalletReceive,
    },
    {
      path: '/wallet/send',
      component: WalletSend
    },
    {
      path: '/wallet/export',
      component: WalletExport
    },
    {
      path: '/post/:txId',
      component: UserPosts,
      props: true
    },
    {
      path: '/my/muted',
      component: UserMuted
    },
    {
      path: '/user/:address',
      component: UserPosts,
      props: true
    },
    {
      path: '/user/likes/:address',
      component: UserLikes,
      props: true
    },
    {
      path: '/user/following/:address',
      component: UserFollowing,
      props: true
    },
    {
      path: '/user/followers/:address',
      component: UserFollowers,
      props: true
    }
  ]
})



