<template>
    <nav v-bind:class="{ 'no-background': !showBackground }">
      <PostDisplayModal
        :item="currentPost">
      </PostDisplayModal>
      <PostReplyModal
        :item="currentPost">
      </PostReplyModal>
      <PostNewModal/>
      <LoginModal/>
      <PinModal @pinEntered="onPinEntered"/>
      <div class="title-mobile"><h1>{{ title }}</h1></div>
      <div class="nav-mobile">
        <a id="nav-toggle" href="javascript:void(0)" @click="showMobileNav = !showMobileNav"><span></span></a>
      </div>
      <ul class="nav-list" v-bind:class="{ 'inactive': !showMobileNav, 'active': showMobileNav }">

      <li v-bind:class="{ 'hidden': !showLogo, 'logo': showLogo }">
          <a href="/">
            <img id="g_logo" src="../../assets/images/logo-small.png" srcset="../../assets/images/logo-small@2x.png 2x, ../../assets/images/logo-small@3x.png 3x" name="g_logo" />
          </a>
        </li>
        <li v-bind:class="{ 'hidden': !title, 'title-header': title }"><h1>{{ title }}</h1></li>
        <li class="home-mobile"><a href="/">Home</a></li>
        <li  v-bind:class="{ 'hidden': !showSearch }">
          <SearchNav :searchText="searchText" v-on:searchTextInput="onSearch"/>
        </li>
      <UserNav/>
      </ul>
    </nav>
</template>

<script>
  import { mapState } from 'vuex'
  import PostDisplayModal from '@/components/Post/DisplayModal'
  import PostReplyModal from '@/components/Post/ReplyModal'
  import PostNewModal from '@/components/Post/NewModal'
  import LoginModal from '@/components/Auth/LoginModal'
  import PinModal from '@/components/Header/PinModal'
  import UserNav from '@/components/Header/UserNav'
  import SearchNav from '@/components/Header/Search'

  export default {
      name: 'HeaderNav',
      props: {
        logo: {
          type: Boolean,
          default: true
        },
        search: {
          type: Boolean,
          default: false
        },
        background: {
          type: Boolean,
          default: true
        },
        title: String,
        searchText: String
      },
      data () {
        return {
          showLogo: this.logo,
          showSearch: this.search,
          showBackground: this.background,
          showMobileNav: false
        }
      },
      components: {
        PostDisplayModal,
        PostReplyModal,
        PostNewModal,
        LoginModal,
        PinModal,
        UserNav,
        SearchNav
      },
      computed: {
        ...mapState([
          'currentPost'
        ])
      },
      methods: {
        onSearch: function (searchTextInput) {
          this.$emit('searchTextInput', searchTextInput)
        },
        onPinEntered () {
          this.$emit('pinEntered')
        }
      }
  };
</script>

<style>
.hidden {
  display: none;
}
h1{
  color: #5f6368;
  display: block;
  font-family: 'Google Sans',sans-serif;
  font-size: 20px;
  font-weight: 400;
  margin: 0;
  padding: 7px 8px 8px 5px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
nav {
    grid-column: span 12;
  	display:relative;
  	padding-top: 0px;
  	padding-left: 13px;
  	padding-right: 35px;
  	background-color: #FAFAFA;
  	height: 50px;
  	border-bottom: 0.5px solid #EBEBEB;
  	display:flex;
  	justify-content: space-between;
}
nav.no-background {
  	background-color: #fff;
  	border-bottom: 0;
}
ul.nav-list {
  width: 100%;
  float: right;
  list-style: none;
  margin: 0;
  padding-top: 1px;
  padding-bottom: 10px;
  top: 0;
  padding-inline-start: 0;
}
nav ul li {
  float: left;
  position: relative;
}
nav ul li.logo {
  margin-top: 4px;
}
nav ul li.title-header {
  margin-top:7px;
}
nav ul li.search {
}
nav ul:not(.item-dropdown-menu-active) li a {
  display: block;
  padding: 0 20px;
  line-height: 30px;
  text-decoration: none;
}
nav ul li a:hover {
}
nav ul li.logo a {
  display: block;
  padding: 0;
  line-height: 30px;
  text-decoration: none;
}
nav li a:hover:not(.button-active) {
    text-decoration: underline;
}
ul.nav-list li a:not(:only-child):after {
  padding-left: 4px;
  content: ' ';
}
ul.nav-list li ul {
  margin: 0;
  padding:0;
  list-style: none;
}
ul.nav-list li ul li {
  background-color: #f9f9f9;
  min-width: 190px;
  margin:0;
  padding: 0;
}
ul.nav-list li ul li a {
  margin: 0;
  padding: 10px 15px;
  color: black;
  text-decoration: none;
}
ul.nav-list li ul li a:hover {
  background-color: #f1f1f1
}
.nav-dropdown {
  display: none;
}
.nav-dropdown-active {
  display: block;
  position: absolute;
  text-align: left;
  right: 0;
  margin-top: 42px;
  margin-right: 20px;
  background-color: #f9f9f9;
  min-width: 190px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}
.home-mobile {
  border: 1px solid #4285f4;
  background: #f9f9f9;
  display: none;
}
.home-mobile a {
  color: black;
  text-decoration: none;
}
.home-mobile a:hover {
  text-decoration: underline;
}
.nav-mobile {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  background: transparent;
  height: 70px;
  width: 70px;
}

.title-mobile {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  background: transparent;
  margin-top: 20px;
  margin-left: 20px;
  height: 70px;
  width: 270px;
}
#nav-toggle {
  position: absolute;
  left: 18px;
  top: 22px;
  cursor: pointer;
  padding: 10px 35px 16px 0px;
}
#nav-toggle span, #nav-toggle span:before, #nav-toggle span:after {
  cursor: pointer;
  border-radius: 1px;
  height: 5px;
  width: 35px;
  background: #000;
  position: absolute;
  display: block;
  content: '';
  transition: all 300ms ease-in-out;
}
#nav-toggle span:before {
  top: -10px;
}
#nav-toggle span:after {
  bottom: -10px;
}
#nav-toggle.active span {
  background-color: transparent;
}
#nav-toggle.active span:before, #nav-toggle.active span:after {
  top: 0;
}
#nav-toggle.active span:before {
  transform: rotate(45deg);
}
#nav-toggle.active span:after {
  transform: rotate(-45deg);
}
 @media only screen and (max-width: 800px) {
   .g_apps {
     width:100%;
   }
   .logo {
     display: none;
   }
	 .nav-mobile {
		 display: block;
	}
  .title-mobile {
    display: block;
 }
  .title-header {
    display: none;
  }
	nav {
     height: 0;
		 width: 100%;
     margin: 0;
		 padding: 70px 0 15px;
	}
	nav ul.inactive {
     padding: 0;
		 display: none;
	}
	nav ul.active {
    padding-inline-start: 0;
    width: 100%;
	  display: block;
	}
  ul.nav-list li {
		 width: 100%;
     margin:0;
		 float: none;
     z-index: 1;
	}
	ul.nav-list li a {
		 padding: 15px;
		 line-height: 20px;
	}
	ul.nav-list li a.signinbox {
     margin-top: 0;
     margin-right: 0;
	}
  ul.nav-list li ul {
    width: 100%;
  }
	ul.nav-list li ul li a {
		 padding-left: 15px;
	}
	 .nav-dropdown {
		 position: static;
	}
  .nav-dropdown-active {
    margin-top: 52px;
    margin-right: 0px;
  }
  .home-mobile {
    display: block;
    z-index: 1;
  }
  .nav-list .button-active {
    color: #000;
    background-color: #f9f9f9;
    border: 0;
    font-family: arial, sans-serif;
    font-size: 13px;
    font-weight: normal;
    height: 20px;
    line-height: 20px;
    list-style-image: none;
    list-style-position: outside;
    list-style-type: none;
    margin: 0;
    padding: 10px 15px;
    text-align: left;
    text-decoration-color: #000;
    text-decoration-line: none;
    text-decoration-style: solid;
    border-radius: 0;
  }
  .nav-list .button-active:hover {
    background-color: #f1f1f1;
    color: #000;
  }
}
 @media screen and (min-width: 801px) {
	.nav-list {
		display: block !important;
	}
}

</style>



