<template>
    <form @submit.prevent="searchForText" class="searchForm">
      <input type="text" value="searchText" name="searchText" title="Search Bitcoin Cash" placeholder="Search Bitcoin Cash for text" v-model="searchText" v-on:input="searchText = $event.target.value" autocomplete="off" />
      <button class="loupe"><img  src="../../assets/images/search.png" srcset="../../assets/images/search@2x.png 2x, ../../assets/images/search@3x.png 3x" /></button>
    </form>
</template>

<script>
  export default {
      name: 'SearchNav',
      props: {
        searchText: String
      },
      data () {
        return {
        }
      },
      mounted () {
      },
      methods: {
        searchForText () {
          if (this.searchText) {
            this.$emit('searchTextInput', this.searchText)
            this.$router.push('/result/' + Base64.encode(this.searchText) + '/0')
          }
        }
      }
  };
</script>

<style>
.searchForm input[type="text"]{
  box-sizing:border-box;
  height:46px;
  width: 630px;
  font-family: sans-serif;
  font-size: 16px;
  padding: 0 20px;
  border: 0.5px solid #d9d9d9;
  box-shadow: 0 4px 2px -2px #e6e6e6;
}
.searchForm input[type="text"]:hover{
  box-shadow: 0px 2px 8px #dddddd;
  outline: none;
}
.loupe{
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  cursor:pointer;
  overflow: hidden;
  outline:none;
  position: absolute;
  top:10px;
  right:20px;
}
@media screen and (max-width: 1024px) {
  .searchForm input[type="text"]{
    box-sizing:border-box;
    height:46px;
    width: 460px;
    font-family: sans-serif;
    font-size: 16px;
    padding: 0 20px;
    border: 0.5px solid #d9d9d9;
    box-shadow: 0 4px 2px -2px #e6e6e6;
  }
}
@media screen and (max-width: 800px) {
  .searchForm input[type="text"]{
    box-sizing:border-box;
    height:46px;
    width: 100%;
    font-family: sans-serif;
    font-size: 16px;
    padding: 0 20px;
    border: 0.5px solid #d9d9d9;
    box-shadow: 0 4px 2px -2px #e6e6e6;
  }
}
</style>



