<template>
    <div class="grid">
      <HeaderNav  v-bind:title="'Export wallet key'" @pinEntered="onPinEntered"/>

      <div class="s_results form" v-if="pinEntered || this.loggedSpendMode()">

        <h3>Raw format</h3>
        <qriously :value="privateKeyRaw" :size="300" />
        <p>{{ privateKeyRaw }}</p>

        <h3>WIF format (compressed)</h3>
        <qriously :value="privateKeyWIF" :size="300" />
        <p>{{ privateKeyWIF }}</p>

        <p style="padding-bottom:20px"></p>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'WalletExport',
  mixins: [],
  components: {
    HeaderNav,
    Footer
  },
  data () {
    return {
      pinEntered: null
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Export wallet keys'
      }
    }
  },
  mounted () {
    this.loginRequired()
  },
  computed: {
    privateKeyRaw: function () {
      if (this.pinEntered || this.loggedSpendMode()) {
        return this.calculatePrivateKey()
      }
    },
    privateKeyWIF: function () {
      if (this.pinEntered || this.loggedSpendMode()) {
        return bitcore.PrivateKey(this.privateKeyRaw).toWIF().toString()
      }
    }
  },
  methods: {
    onPinEntered () {
      this.pinEntered = 1
    }
  }
}
</script>



