  addressesUsernames (context, [ newAddresses ]) {
      let existingUsernames = context.state.usernames
      if (newAddresses.length > 0) {
        console.log('addressesUsernames-store')
        let findQuery = {}
        findQuery['$and'] = []
        findQuery['$and'].push({'out.b0':{ 'op': 106 }})
        findQuery['$and'].push({'out.h1':'6d01'})

        let findAddressesQuery = {}
        findAddressesQuery['$or'] = []
        for (let i = 0; i < newAddresses.length; ++i) {
          if (existingUsernames[newAddresses[i]] === undefined) {
            findAddressesQuery['$or'].push({'in.e.a': ''  + newAddresses[i] + ''})
          }
        }
        findQuery['$and'].push(findAddressesQuery)

        let query = {
          'v': 3,
          'q': {
            'find': findQuery,
            'project': { 'in': 1, 'out.$': 1, 'blk': 1 },
            'limit': 50000
          },
          'r': {
            'f': '[ .[] | {address: .in[0].e.a, username: .out[0].s2, timestamp: .blk.t?} ]'
          }
        }

        let b64 = Base64.encode(JSON.stringify(query))
        let url = window.bitdbNode + b64

        let header = {
          headers: { key: window.bitdbApiKey }
        }

        axios
          .get(url, header)
          .then(response => {
            if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
              let result_a = []
              if (response.data.u !== undefined) {
                result_a = result_a.concat(response.data.u)
              }
              if (response.data.c !== undefined) {
                result_a = result_a.concat(response.data.c)
              }
              let result = result_a.reduce(function(acc, curr) {
                if (curr._id) {
                  var fromMap = acc.map[curr._id];
                  if (!fromMap) {
                    acc.map[curr._id] = fromMap = {
                      count: 0,
                      _id: curr._id
                    }
                    acc.result.push(fromMap)
                  }
                  fromMap.count += parseFloat(curr.count);
                } else {
                  acc.result.push(curr);
                }
                return acc
              }, {
                map: {},
                result: []
              }).result

              let usernames = {}
              for (let i = 0; i < result.length; ++i) {
                if (existingUsernames[result[i].address] === undefined) {
                  usernames[result[i].address] = result[i].username
                }
              }
              context.commit('setUsernames', Object.assign({}, context.state.usernames, usernames))
              context.commit('setCrawledAddresses', context.state.crawledAddresses.concat(newAddresses))
            }
          })
          .catch(error => {
            console.log(JSON.stringify(error))
//            this.memoErrored = true
          })
      }
  },

