<template>
  <div id="replyModal" class="modal">
    <span class="close" @click="closeModal('replyModal')">&times;</span>
    <div class="modal-content">
      <h2 v-if="item">Reply to
        <span v-if="usernames[item.sender]">{{ usernames[item.sender] }}</span>
        <span v-else>{{ item.sender.substr(0, 12) }}...</span>
      </h2>

      <PostListItem
        v-if="item"
        :item="item"
        :showActions="0">
      </PostListItem>

      <PostReplyForm
        v-if="item"
        :item="item">
      </PostReplyForm>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import PostReplyForm from '@/components/Post/ReplyForm'
  import PostListItem from '@/components/Post/ListItem'

  export default {
      name: 'PostReplyModal',
      mixins: [],
      props: {
        item: Object,
      },
      components: {
        PostReplyForm,
        PostListItem
      },
      data () {
        return {
        }
      },
      computed: {
        ...mapState([
          'usernames'
        ])
      },
      methods: {
      }
  };
</script>

<style>
</style>



