<template>
          <!-- The Modal -->
          <div id="pinModal" class="modal pin">
            <!-- Modal content -->
          	<form @submit.prevent="enterPin()">
            <div class="modal-content">
              <span class="close" @click="closeModal('pinModal')">&times;</span>
              <h2>Pin</h2>

              <p style="color:#f11;font-size:small" v-if="errors.length">
                <b>Please correct the following error(s):</b>
                <ul class="errors">
                  <li v-for="error in errors">{{ error }}</li>
                </ul>
              </p>

              <p><input type="password" name="pin" v-model="pin" placeholder="Pin" autofocus/><br />
              <input type="checkbox" value="true" name="rememberPin" v-model="rememberPin"> Remember pin</p>
              <p>
                <input value="Login" type="submit">
                <input value="Close" type="button" @click="closeModal('pinModal')">
              </p>
            </div>
            </form>
          </div>
</template>


<script>
  export default {
      name: 'PinModal',
      mixins: [],
      data () {
        return {
          pin: null,
          rememberPin: null,
          errors: []
        }
      },
      methods: {
        enterPin () {
          this.errors = []
          if (this.rememberPin) {
            localStorage.setItem('user.pin', this.pin)
          } else {
            sessionStorage.setItem('user.pin', this.pin)
          }

          if (this.getAddress() != this.addressFromPrivateKey()) {
            this.errors.push('Incorrect pin')
            sessionStorage.removeItem('user.pin')
            localStorage.removeItem('user.pin')
            this.pin = ''
            return
          }

          this.$emit('pinEntered')
          this.closeModal('pinModal')
        }
      }
  };
</script>

<style>
.pin  input[type="text"] {
    width: 100%;
    height: 34px;
    padding-left: 5px;
    text-align: left;
    font-size: 13px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}
.pin input[type="checkbox"] {
  display: inline;
  margin-top: 15px;
  width: 15px;
}
.pin input[type="submit"] {
    color: #fff;
    background-color: #428bca;
    border-color: #357ebd;
    font-weight: bold;
    height: 36px;
    width: 100px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 10px 11px 0px;
    text-align: center;
}
.pin input[type="button"] {
    background-color: #f2f2f2;
    color: #757575;
    font-weight: bold;
    height: 36px;
    width: 100px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 10px 11px 0px;
    text-align: center;
}
.pin input[type="submit"]:hover {
    border: 1px solid #c6c6c6;
}
.pin input[type="submit"]:focus {
    border: 1px solid #4d90fe;
}
</style>



