// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
window.reverseHex = function reverseHex (hex) {
  let tmpHex = [...hex].reverse().join('')
  let reversedHex = ''
  for (let i = 0; i < tmpHex.length; i += 2) {
    reversedHex += tmpHex[i + 1] + tmpHex[i]
  }
  return reversedHex
}


import Vue from 'vue'
