Vue.config.productionTip = false
Vue.use(VueQriously)
Vue.use(VueHead)
Vue.use(VueInfiniteScroll)

Vue.mixin(memoMixin)
Vue.mixin(modalMixin)
Vue.mixin(walletMixin)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})

Vue.filter('striphtml', function (value) {
  let div = document.createElement('div')
  div.innerHTML = value
  let text = div.textContent || div.innerText || ''
  return text
})
Vue.filter('toTitle', function (text) {
  let resultString = text.split(' ')[0]
  if (text.includes(' ')) {
    resultString = resultString + ' ' + text.substr(text.indexOf(' ') + 1).split(/[.|:|;|-|!|?|<|&|(|)]+\s+/).slice(0, 1).join(' ').split(/\s+http/).slice(0, 1).join(' ').split(/\<br/).slice(0, 1).join(' ')
  }
  return resultString
})
Vue.filter('highlight', function(words, query) {
    return words.replace(query, '<span class="highlight">' + query + '</span>')
});



