<template>
    <div class="grid">
      <HeaderNav  v-bind:title="'Send ' + (networkName || 'BSV')"/>

      <div class="s_results form">

        <p style="color:#f11;font-size:small">
          <b>Poster.cash wallet does not have replay protection. If you want to add replay protection to your transactions please export your private key and import it in another wallet.</b>
        </p>

        <p style="color:#f11;font-size:small" v-if="errors.length">
          <b>Please correct the following error(s):</b>
          <ul class="errors">
            <li v-for="error in errors">{{ error }}</li>
          </ul>
        </p>

        <p style="color:#191;font-size:small" v-if="notifications.length">
          <ul class="notifications">
            <li v-for="notification in notifications">{{ notification }}</li>
          </ul>
        </p>
              <form class="ui form" @submit.prevent="sendTx">

                <div class="field-block">
                  <h4>Address</h4>
                  <input type="text" name="receiverAddress" v-model="receiverAddress" placeholder="Address" required autocomplete="off">
                </div>

                <div class="field-block">
                  <h4>Amount ({{ networkName || 'BSV' }})</h4>
                  <input type="text" name="amount" v-model="amount" :placeholder="'Amount (' + (networkName || 'BSV') + ')'" required autocomplete="off">
                  <p style="color:#666;font-size:small" v-if="amount">({{ amountSat }} satoshis)</p>
                </div>

                <div class="form-buttons">
                  <input value="Send" type="submit" @click="submitted=true" :disabled="submitted">
                </div>
              </form>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'WalletSend',
  mixins: [],
  components: {
    HeaderNav,
    Footer
  },
  data () {
    return {
      networkName: window.networkName,
      receiverAddress: null,
      amount: null,
      submitted: false,
      notifications: [],
      errors: []
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Send ' + (this.networkName || 'BSV')
      }
    }
  },
  mounted () {
    this.loginRequired()
  },
  computed: {
    amountSat: function () {
      if (this.amount) {
        return satoshiBitcoin.toSatoshi(this.amount)
      }
      return
    }
  },
  watch: {
    walletNotification: function (val) {
      for (let i = 0; i < this.walletNotification.length; ++i) {
        if (this.walletNotification[i].type === undefined) {
          continue
        }
        if (this.walletNotification[i].type === 'error') {
          this.errors.push(this.walletNotification[i].notification)
        }
        if (this.walletNotification[i].type === 'success') {
          this.notifications.push(this.walletNotification[i].notification)
          this.receiverAddress = ''
          this.amount = ''
        }
      }
      this.submitted = false
    }
  },
  methods: {
    sendTx () {
      this.errors = []
      this.notifications = []
      this.walletNotification = []

      let toArray = []
      let toElement = {}
      toElement['address'] = this.receiverAddress
      toElement['value'] = parseInt(this.amountSat)
      toArray.push(toElement)

      this.sendTransaction([], toArray)
    }
  }
}
</script>



