<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <UserNav :address="getAddress().substr(12)" />
    </div>

    <div  class="s_results" v-if="mutedUsers.length > 0">
      <p class="section-title">
        My muted users
      </p>
      <div class="stream-item" v-for="(item, index) in mutedUsers">
          <div class="stream-item-header">
            <router-link :to="'/user/' + item" class="account-group">
              <img class="avatar" v-if="profilePictures[item]" :src="profilePictures[item]" alt="">
              <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
                <span class="full-name-group">
                  <strong class="full-name text-truncate" v-if="usernames[item]">{{ usernames[item] }}</strong>
                  <strong class="full-name text-truncate" v-else>{{ item.substr(0, 12) }}...</strong>
                  <span class="user-badges">
                      <strong class="full-name text-truncate" v-if="followedUsers.includes(item)"><i class="fas fa-star following"></i></strong>
                  </span>
                </span>
            </router-link>
            <a href="javascript:void(0);" @click="removeMutedUser(item)">[x]</a>
          </div>
      </div>
    </div>
    <div class="s_results" v-else>
      <div class="n_results">No muted users</div>
    </div>

    <div class="s_right topics">
    </div>

    <Footer/>
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from 'vuex'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'

export default {
  name: 'UserMuted',
  mixins: [],
  props: {
  },
  data () {
    return {
    }
  },
  head: function () {
    return {
      title: {
        inner: 'My muted users'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
        { name: 'twitter:title', content: 'My muted users' },
        { itemprop: 'name', content: 'My muted users' },
        { property: 'og:title', content: 'My muted users' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer,
    UserNav
  },
  mounted () {
    let that = this
    let newAddresses = this.mutedUsers.filter( function( el ) {
      return that.crawledAddresses.indexOf( el ) < 0;
    } )
    if (this.getAddress()) {
      newAddresses.push(this.getAddress().substr(12))
    }
    this.addressesUsernames([ newAddresses ])
    this.addressesProfilePictures([ newAddresses ])
  },
  created() {
  },
  computed: {
    ...mapState([
      'crawledAddresses',
      'followedUsers',
      'mutedUsers',
      'profilePictures',
      'usernames'
    ])
  },
  watch: {
  },
  methods: {
    ...mapActions([
      'removeMutedUser',
      'addressesUsernames',
      'addressesProfilePictures'
    ])
  }
}
</script>

<style>
</style>



