<template>
    <div class="grid">
      <HeaderNav  v-bind:title="'Receive ' + (networkName || 'BSV')"/>

      <div class="s_results form">

        <p style="color:#191;font-size:small" v-if="receivedTxs" v-for="item in receivedTxs.data">
          Transaction received: {{ item.tx.h }}
        </p>

        <p style="color:#911;font-size:small" v-if="walletError">{{ walletError }}</p>

        <qriously :value="getAddress()" :size="300" />
        <p>{{ getAddress() }}</p>
        <p style="padding-bottom:20px;"></p>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'WalletReceive',
  mixins: [],
  components: {
    HeaderNav,
    Footer
  },
  data () {
    return {
      networkName: window.networkName,
      moneyButtonEnabled: false,
      walletError: sessionStorage.getItem('walletError')
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Receive ' + (this.networkName || 'BSV') + ' Payment'
      }
    }
  },
  mounted () {
    this.loginRequired()
    this.getLiveAddressInfo()
    sessionStorage.removeItem('walletError')
  },
  watch: {
    moneyButtonEnabled: function (val) {
    }
  },
  methods: {
  }
}
</script>



