<template>
  <div id="displayModal" class="modal">
    <span class="close" @click="closeModal('displayModal')">&times;</span>
    <div class="modal-content">
      <PostDisplay
        v-if="item"
        :item="item">
      </PostDisplay>
    </div>
  </div>
</template>


<script>
  import PostDisplay from '@/components/Post/Display'

  export default {
      name: 'PostDisplayModal',
      mixins: [],
      props: {
        item: Object
      },
      components: {
        PostDisplay
      },
      data () {
        return {
        }
      },
      mounted () {
      },
      methods: {
      }
  };
</script>

<style>
</style>



