export default {
  allFollowedUsers: (state, getters) => {
    return state.followedUsersOnchain.concat(state.followedUsersLocal)
  },
  allFollowedUsersLocal: (state, getters) => {
    return state.followedUsersLocal
  },
  allFollowedUsersOnchain: (state, getters) => {
    return state.followedUsersOnchain
  }
}



