<template>
  <div class="stream-item" v-bind:class="{ 'stream-item-single': (isLarge || !showActions) }">

    <div class="stream-item-header">
      <router-link :to="'/user/' + item.sender" class="account-group">
        <img class="avatar" v-if="profilePictures[item.sender]" :src="profilePictures[item.sender]" alt="">
        <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
          <span class="full-name-group">
            <strong class="full-name text-truncate" v-bind:class="{ 'full-name-large': isLarge }" v-if="usernames[item.sender]">{{ usernames[item.sender] }}</strong>
            <strong class="full-name text-truncate" v-bind:class="{ 'full-name-large': isLarge }" v-else>{{ item.sender.substr(0, 12) }}...</strong>
            <span class="user-badges">
                <strong class="full-name text-truncate" v-if="followedUsers.includes(item.sender) && !isLarge"><i class="fas fa-star following"></i></strong>
            </span>
          </span>
      </router-link>
      <span v-if="!isLarge">
        <span class="date" v-if="item.timestamp">&nbsp;- {{ timeSince(item.timestamp) }}</span>
        <span class="date" v-else>&nbsp;- now</span>
      </span>
      <span class="item-dropdown" v-if="!isLarge">
        <a href="javascript:void(0)" class="item-dropdown-opener" @click="showItemDropdown = !showItemDropdown"><i class="fas fa-chevron-down"></i></a>
          <ul v-bind:class="{ 'item-dropdown-menu': !showItemDropdown, 'item-dropdown-menu-active': showItemDropdown }" id="itemDropdown">
            <li><a :href="'https://bchsvexplorer.com/tx/' + item.txId">View in block explorer</a></li>
            <li><a href="javascript:void(0)" @click="addMutedUser(item.sender);showItemDropdown = !showItemDropdown">Mute user</a></li>
          </ul>
        </span>
      <span class="large-following" v-if="isLarge">
        <button class="button-active" v-if="followedUsers.includes(item.sender)" @click="unfollowAddress(item.sender)">Following</button>
        <button class="button" v-else @click="followAddress(item.sender)">Follow</button>
      </span>
    </div>
    <div class="stream-item-text-container" @click="displayModal(item, 'displayModal')">
      <p class="item-text" v-bind:class="{ 'item-text-large': isLarge }" v-if="item.prefix === '6d02'">{{ item.content1 }}</p>
      <p class="item-text" v-bind:class="{ 'item-text-large': isLarge }" v-else>{{ item.content2 }}</p>
    </div>
    <div class="stream-item-text-container" v-if="isLarge">
      <p class="date-large" v-if="item.timestamp">{{ largeDatetime(item.timestamp) }}</p>
      <p class="date-large" v-else>{{ largeDatetime(Date.now()/1000) }}</p>
    </div>

    <div class="stream-item-footer" v-if="showActions">
      <span>
        <i class="fas fa-heart icon" v-if="myPostLikes[item.txId]"></i><i class="far fa-heart icon" v-else @click="likeItem(item.txId, item.sender, 0)"></i><span class="post-action-count"><span v-if="likesCount[item.txId]">{{ likesCount[item.txId] }}</span><span v-else>&nbsp;</span></span>
        <i class="fas fa-comment icon" v-if="myPostReplies[item.txId]" @click="replyModal(item, 'replyModal')"></i><i class="far fa-comment icon" v-else @click="replyModal(item, 'replyModal')"></i><span class="post-action-count"><span v-if="repliesCount[item.txId]">{{ repliesCount[item.txId] }}</span><span v-else>&nbsp;</span></span>
          <form class="tip-form" @submit.prevent="likeItem(item.txId, item.sender, tipAmount)">
            <input type="number" min="546" required autocomplete="off" class="tip-input" v-model="tipAmount" placeholder="Tip">
            <button class="fas fa-hand-holding-usd icon"></button>
          </form>
      </span>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from 'vuex'

export default {
  name: 'PostListItem',
  mixins: [],
  props: {
    showActions: {
      type: Number,
      default: 1
    },
    isLarge: {
      type: Number,
      default: 0
    },
    item: Object,
    topics: Array
  },
  components: {
  },
  data () {
    return {
      followedTopics: this.topics || [],
      tipAmount: '',
      showItemDropdown: false
    }
  },
  mounted () {
  },
  computed: {
    ...mapState([
      'followedUsers',
      'myPostLikes',
      'myPostReplies',
      'likesCount',
      'repliesCount',
      'usernames',
      'profilePictures'
    ])
  },
  methods: {
    ...mapActions([
      'addMutedUser',
      'changeCurrentPost',
      'addToFollowedUsers',
      'removeFromFollowedUsers',
      'addToMyLikes'
    ]),
    timeSince(unix_timestamp) {
      const date = new Date(parseInt(unix_timestamp) * 1000)
      const seconds = Math.floor((new Date() - date) / 1000)
      let interval = Math.floor(seconds / 31536000)
      if (interval >= 1) {
        return this.unixTimeToDatetime(unix_timestamp).substr(0, 11)
      }
      interval = Math.floor(seconds / 2592000)
      if (interval >= 1) {
        return this.unixTimeToDatetime(unix_timestamp).substr(5, 6)
      }
      interval = Math.floor(seconds / 86400)
      if (interval >= 1) {
        return  this.unixTimeToDatetime(unix_timestamp).substr(5, 6)
      }
      interval = Math.floor(seconds / 3600)
      if (interval >= 1) {
        return interval + "h"
      }
      interval = Math.floor(seconds / 60)
      if (interval >= 1) {
        return interval + "m"
      }
      return Math.floor(seconds) + "s"
    },
    largeDatetime (unix_timestamp) {
      let a = new Date(parseInt(unix_timestamp) * 1000)
      let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let year = a.getFullYear()
      let month = months[a.getMonth()]
      let date = a.getDate()
      let hour = ('0' + a.getHours()).substr(-2)
      let min = ('0' + a.getMinutes()).substr(-2)
      let time =   hour + ':' + min + ' - ' + date + ' ' + month + ' ' + year
      return time
    },
    unixTimeToDatetime (unix_timestamp) {
      let a = new Date(parseInt(unix_timestamp) * 1000)
      let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let year = a.getFullYear()
      let month = months[a.getMonth()]
      let date = a.getDate()
      let hour = ('0' + a.getHours()).substr(-2)
      let min = ('0' + a.getMinutes()).substr(-2)
      let time = year + ' ' + month + ' ' + date + ' ' + hour + ':' + min
      return time
    },
    likeItem (txId, sender, amount) {
      if (this.loginRequired()) {
        return
      }
      this.likePost(txId, sender, amount)
      this.tipAmount = ''
      this.addToMyLikes(txId)
    },
    followAddress (address) {
      if (this.loginRequired()) {
        return
      }
      this.follow(address)
      this.addToFollowedUsers(address)
    },
    unfollowAddress (address) {
      if (this.loginRequired()) {
        return
      }
      this.unfollow(address)
      this.removeFromFollowedUsers(address)
    },
    displayModal (item, modal) {
      this.changeCurrentPost(item)
      this.openModal(modal)
    },
    replyModal (item, modal) {
      if (this.loginRequired()) {
        return
      }
      if (this.profilePictures[item.sender] !== undefined) {
        item['picture'] = this.profilePictures[item.sender]
      }
      if (this.usernames[item.sender] !== undefined) {
        item['username'] = this.usernames[item.sender]
      } else {
        item['username'] = item.sender
      }
      if (document.getElementById('displayModal').style.display === 'block') {
        this.closeModal('displayModal')
      }
      this.changeCurrentPost(item)
      this.openModal(modal)
    }
  }
}
</script>
<style>
</style>



