<template>
  <span>
    <PostListItem
      v-for="(item, index) in resultParentItems"
      :key="index"
      :item="item">
    </PostListItem>

    <PostListItem
      :item="item"
      :isLarge="1">
    </PostListItem>

    <PostListItem
      v-for="(item, index) in resultBitdbItems"
      :key="index"
      :item="item">
    </PostListItem>

      <div class="n_results preloader" v-if="loading === true"></div>
  </span>
</template>


<script>
import { mapState, mapActions } from 'vuex'
import PostListItem from '@/components/Post/ListItem'

  export default {
      name: 'PostDisplay',
      mixins: [],
      components: {
        PostListItem
      },
      props: {
        item: Object,
      },
      data () {
        return {
          offset: 0,
          page: 1,
          itemsPerPage: 20,
          resultParentItems: [],
          resultBitdbItems: undefined,
          loading: false,
          errored: false,
          scrollLoading: false
        }
      },
      computed: {
        ...mapState([
          'crawledAddresses',
          'crawledTxIds',
          'usernames'
        ])
      },
      mounted () {
        this.resultParentItems = []
        this.resultBitdbItems = []
        if (this.item.prefix === '6d03') {
          this.postParent()
        }
        this.repliesToPosts()
        this.setMeta(this.item)
      },
      watch: {
        item: function (val) {

          this.resultParentItems = []
          this.resultBitdbItems = []
          if (this.item.prefix === '6d03') {
            this.postParent()
          }
          this.repliesToPosts()
          this.setMeta(val)
        }
      },
      methods: {
        ...mapActions([
          'changeCurrentPost',
          'myLikes',
          'myReplies',
          'likesCountPerPost',
          'repliesCountPerPost',
          'addressesUsernames',
          'addressesProfilePictures'
        ]),
        setMeta (item) {
          let username = item.sender
          if (this.usernames[item.sender]) {
            username = this.usernames[item.sender]
          }
          let content = item.content2
          if (item.prefix === '6d02') {
            content = item.content1
          }
          if (document.getElementById('displayModal').style.display === 'block') {
            document.title = username + ' on Poster: "' + content + '"'
            window.history.pushState('', '', '/post/' + item.txId)
          }
        },
        postParent () {
          this.errored = false

          let query = {
            'v': 3,
            'q': {
          		'find': {
          			'$and': [{
          					'out.b0': {
          						'op': 106
          					}
          				},
          				{
          					'tx.h': reverseHex(this.item.parentTxId)
          				}
          			]
          		},
              'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
          	},
            'r': {
              'f': '[ .[] | {prefix: .out[0].h1, sender: .in[0].e.a, parentTxId: .out[0].h2, content1: .out[0].s2, content2: .out[0].s3, timestamp: .blk.t?, txId: .tx.h} ]'
            }
          }

          let b64 = Base64.encode(JSON.stringify(query))
          let url = window.bitdbNode + b64

          let header = {
            headers: { key: window.bitdbApiKey }
          }

          axios
            .get(url, header)
            .then(response => {
              if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
                let newBitdbItems = []
                if (response.data.u !== undefined) {
                  newBitdbItems = newBitdbItems.concat(response.data.u.reverse())
                }
                if (response.data.c !== undefined) {
                  newBitdbItems = newBitdbItems.concat(response.data.c)
                }

                // populate usernames and txIds
                let newAddresses = []
                let newTxIds = []
                for (let i = 0; i < newBitdbItems.length; ++i) {
                  if(newAddresses.indexOf(newBitdbItems[i].sender) === -1) {
                    newAddresses.push(newBitdbItems[i].sender)
                  }
                  if(newTxIds.indexOf(newBitdbItems[i].txId) === -1) {
                    newTxIds.push(newBitdbItems[i].txId)
                  }
                }
                let that = this
                newAddresses = newAddresses.filter( function( el ) {
                  return that.crawledAddresses.indexOf( el ) < 0;
                } )
                newTxIds = newTxIds.filter( function( el ) {
                  return that.crawledTxIds.indexOf( el ) < 0;
                } )

                this.myLikes([ this.getAddress(), newTxIds ])
                this.myReplies([ this.getAddress(), newTxIds ])
                this.likesCountPerPost([ newTxIds ])
                this.repliesCountPerPost([ newTxIds ])
                this.addressesUsernames([ newAddresses ])
                this.addressesProfilePictures([ newAddresses ])
                this.resultParentItems = newBitdbItems
              }
            })
            .catch(error => {
              this.errored = true
            })
        },
        repliesToPosts (offset = 0) {
          if (offset === 0) {
            this.loading = true
          }
          this.errored = false

          let query = {
            'v': 3,
            'q': {
              'find': {
                "$and": [{
        					"out.b0": {
        						"op": 106
        					}
        				}, {
        					"out.h1": "6d03"
        				}, {
        					"out.h2": reverseHex(this.item.txId)
                }]
              },
              'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
              'skip': parseInt(offset),
              'limit': this.itemsPerPage
            },
            'r': {
              'f': '[ .[] | {prefix: .out[0].h1, sender: .in[0].e.a, parentTxId: .out[0].h2, content1: .out[0].s2, content2: .out[0].s3, timestamp: .blk.t?, txId: .tx.h} ]'
            }
          }

          let b64 = Base64.encode(JSON.stringify(query))
          let url = window.bitdbNode + b64

          let header = {
            headers: { key: window.bitdbApiKey }
          }

          axios
            .get(url, header)
            .then(response => {
              if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
                let newBitdbItems = []
                if (response.data.u !== undefined) {
                  newBitdbItems = newBitdbItems.concat(response.data.u.reverse())
                }
                if (response.data.c !== undefined) {
                  newBitdbItems = newBitdbItems.concat(response.data.c)
                }

                // populate usernames and txIds
                let newAddresses = []
                let newTxIds = []
                for (let i = 0; i < newBitdbItems.length; ++i) {
                  if(newAddresses.indexOf(newBitdbItems[i].sender) === -1) {
                    newAddresses.push(newBitdbItems[i].sender)
                  }
                  if(newTxIds.indexOf(newBitdbItems[i].txId) === -1) {
                    newTxIds.push(newBitdbItems[i].txId)
                  }
                }
                let that = this
                newAddresses = newAddresses.filter( function( el ) {
                  return that.crawledAddresses.indexOf( el ) < 0;
                } )
                newTxIds = newTxIds.filter( function( el ) {
                  return that.crawledTxIds.indexOf( el ) < 0;
                } )

                this.myLikes([ this.getAddress(), newTxIds ])
                this.myReplies([ this.getAddress(), newTxIds ])
                this.likesCountPerPost([ newTxIds ])
                this.repliesCountPerPost([ newTxIds ])
                this.addressesUsernames([ newAddresses ])
                this.addressesProfilePictures([ newAddresses ])
                this.resultBitdbItems = this.resultBitdbItems.concat(newBitdbItems)
              }
              this.offset += this.itemsPerPage
              this.loading = false
              this.scrollLoading = false;
            })
            .catch(error => {
              this.errored = true
              this.loading = false
            })
        },
        loadMore() {
          this.scrollLoading = true;
          if (this.loading === false) {
            this.repliesToPosts(this.offset)
          }
        }
      }
  };
</script>

<style>
</style>



