<template>
            <!-- Modal content -->
            <form class="ui form" @submit.prevent="login" v-if="mode === 'username'">

            <div v-if="isNewUser">
              <p style="color:#811;font-weight:bold;font-size:small">Poster.cash does not store any user data. We cannot recover your user data if you lose it.</p>
            </div>

              <h4 v-if="isNewUser">Sign up</h4>
              <h4 v-else>Login</h4>
              <p>
                <input type="text" name="username" v-model="username" placeholder="Username" required autocomplete="off">
              </p>
              <p>
                <input type="password" name="password" v-model="password" placeholder="Password" required autocomplete="off">
              </p>
              <p>
                <input type="text" name="pin" v-model="pin" placeholder="Pin code" required autocomplete="off">
              </p>
              <p>
                <input type="checkbox" value="true" name="rememberUser" v-model="rememberUser"> Remember username and password</input>
              </p>

              <div class="form-buttons">
                <input value="Sign up" type="submit" v-if="isNewUser">
                <input value="Log in" type="submit" v-else>
                <p style="color:#666;font-size:small">Are you <a href="javascript:void(0)" @click="newUser()">new user</a>?</p>
                <p style="color:#666;font-size:small">Log in with <a href="javascript:void(0)" @click="modeChange('privateKey')">Private Key</a></p>
              </div>
            </form>

            <form class="ui form" @submit.prevent="loginPK" v-else-if="mode === 'privateKey'">
            <div v-if="isNewUser">
              <p style="color:#811;font-weight:bold;font-size:small">Poster.cash does not store any user data. We cannot recover your user data if you lose it.</p>
              <p style="color:#811;font-weight:bold;font-size:small">Poster.cash consolidates your outputs. If you import Memo.cash wallet, this may result in big fee in your first action.</p>
            </div>

              <h4 v-if="isNewUser">Sign up with private key</h4>
              <h4 v-else>Login with private key</h4>
              <p>
                <input type="text" name="privateKey" v-model="privateKey" placeholder="Private Key" required autocomplete="off">
              </p>

              <p>
                <select name="privateKeyFormat" v-model="privateKeyFormat">
                  <option value="raw">Raw</option>
                  <option value="wif">WIF</option>
                </select>
              </p>

              <p>
                <input type="text" name="pKPin" v-model="pKPin" placeholder="Pin code" required autocomplete="off">
              </p>

              <p>
                <input type="checkbox" value="true" name="rememberPrivateKey" v-model="rememberPrivateKey"> Remember private key
              </p>

              <div class="form-buttons">
                <input value="Sign up" type="submit" v-if="isNewUser">
                <input value="Log in" type="submit" v-else>
                <p style="color:#666;font-size:small">Are you <a href="javascript:void(0)" @click="newUser()">new user</a>?</p>
                <p style="color:#666;font-size:small">Log in with <a href="javascript:void(0)" @click="modeChange('username')">Username and password</a></p>
              </div>
            </form>
</template>

<script>
export default {
  name: 'LogInForm',
  mixins: [],
  components: {
  },
  data () {
    return {
      mode: 'username',
      isNewUser: false,
      username: null,
      password: null,
      pin: null,
      rememberUser: null,
      privateKey: null,
      privateKeyFormat: 'raw',
      pKPin: null,
      rememberPrivateKey: null,
      notification: {
        message: '',
        type: ''
      }
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Create or login to Bitcoin Cash wallet'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Oyo.cash' },
        { name: 'description', content: 'Create or login to Bitcoin Cash wallet using username and password. All in browser wallet', id: 'desc' },
        { name: 'twitter:title', content: 'Create or login to Bitcoin Cash wallet' },
        { n: 'twitter:description', c: 'Create or login to Bitcoin Cash wallet using username and password. All in browser wallet' },
        { itemprop: 'name', content: 'Create or login to Bitcoin Cash wallet' },
        { itemprop: 'description', content: 'Create or login to Bitcoin Cash wallet using username and password. All in browser wallet' },
        { property: 'og:title', content: 'Create or login to Bitcoin Cash wallet' }
      ]
    }
  },
  mounted () {
    if (this.loggedViewMode() || this.loggedSpendMode()) {
      this.closeModal('loginModal')
    }
  },
  methods: {
    modeChange (mode) {
      this.mode = mode
    },
    newUser () {
      this.isNewUser = !this.isNewUser
    },
    login () {
      if (this.username && this.password && this.pin) {
        const userPasswordHash = bitcore.crypto.Hash.sha256(Buffer.from(this.username + this.password))
        const pinHash = bitcore.crypto.Hash.sha256(Buffer.from(this.pin))
        const buffArray = [userPasswordHash, pinHash]
        const hash = bitcore.crypto.Hash.sha256(Buffer.concat(buffArray))
        const bn = bitcore.crypto.BN.fromBuffer(hash)
        const pk = bitcore.PrivateKey(bn).toString()
        const address = bitcore.PrivateKey(bn).toAddress().toString()
        const userPasswordHashHex = userPasswordHash.toString('hex')
        if (this.rememberUser) {
          localStorage.setItem('user.address', address)
          localStorage.setItem('user.username', address.substring(12, 18) + '...')
          localStorage.setItem('user.userPasswordHash', userPasswordHashHex)
        } else {
          sessionStorage.setItem('user.address', address)
          sessionStorage.setItem('user.username', address.substring(12, 18) + '...')
          sessionStorage.setItem('user.userPasswordHash', userPasswordHashHex)
        }
        sessionStorage.setItem('user.pin', this.pin)

        this.closeModal('loginModal')
        location.reload()
      } else {
        this.username = this.password = this.pin = ''
      }
    },
    loginPK () {
      if (this.privateKey && this.privateKeyFormat && this.pKPin) {
        let pk = null
        if (this.privateKeyFormat === 'raw') {
          pk = bitcore.PrivateKey(this.privateKey)
        } else if (this.privateKeyFormat === 'wif') {
          pk = bitcore.PrivateKey.fromWIF(this.privateKey)
        }
        const address = pk.toAddress().toString()
        const encryptedPrivateKey = CryptoJS.AES.encrypt(pk.toString(), this.pKPin.toString())
        if (this.rememberPrivateKey) {
          localStorage.setItem('user.address', address)
          localStorage.setItem('user.username', address.substring(12, 18) + '...')
          localStorage.setItem('user.privateKey', encryptedPrivateKey)
        } else {
          sessionStorage.setItem('user.address', address)
          sessionStorage.setItem('user.username', address.substring(12, 18) + '...')
          sessionStorage.setItem('user.privateKey', encryptedPrivateKey)
        }
        sessionStorage.setItem('user.pin', this.pKPin)

        this.closeModal('loginModal')
        location.reload()
      } else {
        this.privateKey = this.pKPin = ''
      }
    }
  }
}
</script>



