<template>
  	<form @submit.prevent="newItem(content)" class="post-form" @click='loginRequired'>
      <p>
        <textarea class="post" name="content" v-model="content" placeholder="What's happening" :maxlength="217" required autofocus></textarea>
        <input class="button-active" value="Post" type="submit" @click="txBroadcasting=true" :disabled="txBroadcasting">
      </p>
      <p>&nbsp;</p>
    </form>
</template>


<script>
  export default {
      name: 'PostNewForm',
      mixins: [],
      components: {
      },
      data () {
        return {
          content: null
        }
      },
      methods: {
        newItem (content) {
          if (this.loginRequired()) {
            this.txBroadcasting = false
            return
          }
          if (!content) {
            this.txBroadcasting = false
            return
          }
          this.newPost(content)
          this.content = ''
          this.closeModal('newModal')
        }
      }
  };
</script>

<style>
</style>



