<template>
  <span>
    <div class="topic-title">
      <span v-if="usernames[address]">{{ usernames[address] }}</span>
      <span v-else>{{ address.substr(0, 12) }}...</span>
    </div>
    <div class="topic-name" v-if="getAddress() && address != getAddress().substr(12)">
      <button class="button-active" v-if="followedUsers.includes(address)" @click="unfollowAddress(address)">Following</button>
      <button class="button" v-else @click="followAddress(address)">Follow</button>
      <button class="button-active" v-if="mutedUsers.includes(address)" @click="removeMutedUser(address)">Muted</button>
      <button class="button" v-else @click="addMutedUser(address)">Mute</button>
    </div>
    <div class="topics">
      <div class="topic-name"><router-link :to="'/user/' + address">Posts</router-link></div>
      <div class="topic-name"><router-link :to="'/user/likes/' + address">Likes</router-link></div>
      <div class="topic-name"><router-link :to="'/user/following/' + address">Following</router-link></div>
      <div class="topic-name"><router-link :to="'/user/followers/' + address">Followers</router-link></div>
      <div class="topic-name"><router-link :to="'/my/muted'" v-if="getAddress() && address == getAddress().substr(12)">Muted</router-link></div>
    </div>
  </span>
</template>

<script>
import { mapState, mapActions } from 'vuex'

  export default {
      name: 'UserNav',
      props: {
        address: String
      },
      data () {
        return {
        }
      },
      components: {
      },
      computed: {
        ...mapState([
          'followedUsers',
          'mutedUsers',
          'usernames'
        ])
      },
      watch: {
        address: function (val) {
          this.addressesUsernames([ val ])
          this.addressesProfilePictures([ val ])
        }
      },
      methods: {
        ...mapActions([
          'addMutedUser',
          'removeMutedUser',
          'addToFollowedUsers',
          'removeFromFollowedUsers',
          'addressesUsernames',
          'addressesProfilePictures'
        ]),
        followAddress (address) {
          if (this.loginRequired()) {
            return
          }
          this.follow(address)
          this.addToFollowedUsers(address)
        },
        unfollowAddress (address) {
          if (this.loginRequired()) {
            return
          }
          this.unfollow(address)
          this.removeFromFollowedUsers(address)
        }
      }
  };
</script>

<style>
</style>



