<template>
  <div class="grid intro">
    <HeaderNav/>

      <div class="intro_left">
        <p class="intro-slogan"><i class="intro-icon fas fa-search"></i> Follow your interests.</p>
        <p class="intro-slogan"><i class="intro-icon fas fa-users"></i> See what people are talking about.</p>
        <p class="intro-slogan"><i class="intro-icon far fa-comments"></i> Join the conversation.</p>
        <p class="intro-slogan"><i class="intro-icon fab fa-btc"></i> Be rewarded and reward others.</p>
        <p class="intro-slogan"><i class="intro-icon fas fa-crown"></i> Own your network.</p>
      </div>

      <div class="intro_right">
        <p class="intro-big-slogan">See whats happening in the world right now</p>
        <p class="intro-slogan">Join Poster today.</p>

        <a href="javascript:void(0)" @click="openModal('loginModal')" class="button-active"> Sign up </a>

        <p class="intro-slogan">or start exploring</p>
        <form @submit.prevent="addTopic">
          <p>
            <input type="text" name="newTopic" v-model="newTopic" placeholder="satoshi" required autocomplete="off">
          </p>
          <p>
            <button class="button-active">Explore</button>
          </p>
        </form>
      </div>

    <Footer/>
  </div>
</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'Intro',
  mixins: [],
  props: {
  },
  data () {
    return {
      newTopic: null,
      followedTopics: []
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Poster. Bitcoin Cash made social'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
        { name: 'description', content: 'Poster is client for the Memo protocol. Get the latest news live streight out of the Bitcoin blockchain.', id: 'desc' },
        { name: 'twitter:title', content: 'Poster. Bitcoin Cash made social' },
        { n: 'twitter:description', c: 'Poster is client for the Memo protocol. Get the latest news live streight out of the Bitcoin blockchain.'},
        { itemprop: 'name', content: 'Poster. Bitcoin Cash made social' },
        { itemprop: 'description', content: 'Poster is client for the Memo protocol. Get the latest news live streight out of the Bitcoin blockchain.' },
        { property: 'og:title', content: 'Poster. Bitcoin Cash made social' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer
  },
  mounted () {
    if (this.loggedViewMode() || this.loggedSpendMode()) {
      this.$router.push('/home')
    }
    if (localStorage.getItem('followedTopics')) {
      this.followedTopics = JSON.parse(localStorage.getItem('followedTopics'))
      if (this.followedTopics.length > 0) {
        this.$router.push('/home')
      }
    }
  },
  created() {
  },
  computed: {
  },
  watch: {
  },
  methods: {
    addTopic () {
      if (this.newTopic && !this.followedTopics.includes(this.newTopic)) {
        this.followedTopics.push(this.newTopic)
        localStorage.setItem('followedTopics', JSON.stringify(this.followedTopics))
        this.newTopic = ''
        this.$router.push('/home')
      }
    }
  }
}
</script>

<style>
.intro_left {
  grid-column: span 6;
  font-family: arial, sans-serif;
  margin: 15px 15px 51px;
  padding: 20px 20px;
  background: #ffffff;
}
.intro_right {
  grid-column: span 6;
  margin: 15px 15px 51px 15px;
  padding: 20px 20px;
  background: #ffffff;
}
.intro-icon {
  width: 50px;
}
.intro-slogan {
  font-family: arial, sans-serif;
  font-size: 20px;
  line-height: 26px;
}
.intro-big-slogan {
  font-family: arial, sans-serif;
  font-size: 32px;
  line-height: 40px;
}
.intro_right input {
  font-size: 18px;
  height:28px;
  border: 1px solid;
  width: 280px;
}
@media screen and (max-width: 980px) {
  .intro_left {
    order: 2;
    grid-column: span 12;
    margin: 15px 15px 51px 15px;
  }
  .intro_right {
    order: 1;
    grid-column: span 12;
    margin: 15px 15px 0 15px;
  }

}
</style>



