<template>
  	<form @submit.prevent="replyItem(item.txId, content)" class="post-form" @click='loginRequired'>
      <p>
        <textarea class="post" name="content" v-model="content" placeholder="Post your reply" :maxlength="184" required autofocus></textarea>
        <input class="button-active" value="Reply" type="submit" @click="txBroadcasting=true" :disabled="txBroadcasting">
      </p>
      <p>&nbsp;</p>
    </form>
</template>


<script>
import { mapActions } from 'vuex'

  export default {
      name: 'PostReplyForm',
      mixins: [],
      components: {
      },
      props: {
        item: Object
      },
      data () {
        return {
          content: null
        }
      },
      watch: {
        item: function (val) {
          this.content = ''
        }
      },
      methods: {
        ...mapActions([
          'addToMyReplies'
        ]),
        replyItem (txId, content) {
          if (this.loginRequired()) {
            return
          }
          this.replyPost(txId, content)
          this.addToMyReplies(txId)
          this.content = ''
          this.closeModal('replyModal')
        }
      }
  };
</script>

<style>
</style>



