<template>
  <footer>
  	<p>
      <span class="left">Powered by bitdb</span>
      <span style="float:right; margin-right: 10px;" v-if="networkName === 'BSV'">Network: BSV</span>
      <span style="float:right; margin-right: 10px;" v-else-if="networkName === 'BCH'">Network: BCH</span>
      <span style="float:right; margin-right: 10px;" v-else>Network: Custom</span>
    </p>

  	<ul>
      	<li class="left"><router-link to="/pages/about">About</router-link></li>
      	<li><router-link to="/pages/disclaimer">Disclaimer</router-link></li>
  		  <li class="right"><router-link to="/pages/settings">Settings</router-link></li>
        <li class="right" v-if="networkName !== 'BSV'"><a href="javascript:void(0)" @click="switchNetwork('BSV')">Switch to Bitcoin SV</a></li>
        <li class="right" v-else><a href="javascript:void(0)" @click="switchNetwork('BCH')">Switch to Bitcoin Cash</a></li>
  	</ul>
  </footer>
</template>

<script>
  export default {
    name: 'Footer',
    data () {
      return {
        networkName: window.networkName
      }
    },
    components: {
    },
    methods: {
      switchNetwork (networkName) {
        let bitdbNode = ''
        if (networkName === 'BSV') {
          bitdbNode = window.bitdbNodeDefault
          bitsocketNode = window.bitsocketNodeDefault
          blockExplorerRpc = window.blockExplorerRpcDefault
        } else if (networkName === 'BCH') {
          bitdbNode = window.bitdbBCHNodeDefault
          bitsocketNode = window.bitsocketBCHNodeDefault
          blockExplorerRpc = window.blockExplorerBCHRpcDefault
        } else {
          return
        }

        if (bitdbNode && bitdbNode != window.bitdbNodeDefault) {
          localStorage.setItem('networkName', networkName)
          localStorage.setItem('bitdbNode', bitdbNode)
          localStorage.setItem('bitsocketNode', bitsocketNode)
          localStorage.setItem('blockExplorerRpc', blockExplorerRpc)
          window.bitdbNode = bitdbNode
          window.bitsocketNode = bitsocketNode
          window.blockExplorerRpc = blockExplorerRpc
        } else {
          localStorage.removeItem('networkName')
          localStorage.removeItem('bitdbNode')
          localStorage.removeItem('bitsocketNode')
          localStorage.removeItem('blockExplorerRpc')
          window.networkName = networkNameDefault
          window.bitdbNode = bitdbNodeDefault
          window.bitsocketNode = bitsocketNodeDefault
          window.blockExplorerRpc = blockExplorerRpcDefault
        }
        location.reload()
      }
    }
  };
</script>

<style>
footer {
    color: rgba(0,0,0,.54);
    background: #f2f2f2;
    position: fixed;
    width: 100%;
    bottom: 0;
    height: 70px;
    margin: 0;
}

footer p {
    border-top: 1px solid #e4e4e4;
    font-size: 15px;
    margin-bottom: 0;
    line-height: 35px;
    margin-top: 0;
    padding-left: 27px;
    text-align: left;
}

footer ul {
    border-top: 1px solid #e4e4e4;
    padding-left: 0px;
    list-style-type:none;
    margin:0;
    margin-right: 10px;
    margin-inline-end:10px;
    padding-top:10px;
    padding-bottom:10px;
    overflow:hidden;
    top:0;
}

footer li {
    display: inline-block;
    float: left;
    padding-left: 27px;
}

footer a {
    text-decoration: none;
    color: #666;
}

footer li.right{
    float: right;
}
@media screen and (max-width: 800px) {
  footer p {
      padding-left: 21px;
  }
  footer li {
      padding-left: 21px;
  }
}
</style>



