<template>
  <div class="form">
    <p style="color:#191;font-size:small" v-if="updated">
      Profile updated
    </p>
  	<form @submit.prevent="setValues">
    <div class="field-block">
      <h4>Name</h4>
  	   <input type="text" name="name" v-model="name" title="Name" placeholder="Name" :maxlength="77"/>
    </div>
    <div class="field-block">
      <h4>Profile Picture</h4>
      <input type="text" name="profilePicture" v-model="profilePicture" title="Profile picture" placeholder="https://" :maxlength="217"/>
    </div>
    <div class="field-block">
      <h4>Profile</h4>
      <input type="text" name="profile" v-model="profile" title="Profile" placeholder="Profile" :maxlength="217"/>
    </div>
    <div class="form-buttons">
      <input value="submit" type="submit">
    </div>
  </form>
  </div>
</template>

<script>
  export default {
    name: 'EditProfile',

    data () {
      return {
        name: null,
        oldName: null,
        profilePicture: null,
        oldProfilePicture: null,
        profile: null,
        oldProfile: null,
        updated: false
      }
    },
    components: {
    },
    mounted () {
      this.getName()
      this.getProfile()
      this.getProfilePicture()
    },
    methods: {
      getName () {
        this.errored = false
        let query = {
          'v': 3,
          'q': {
            'find': {
              '$and': [{
                  'out.b0': {
                    'op': 106
                  }
                }, {
  								'$or': [{
  									'out.h1': '6d01'
  								}]
  							}, {
                  'in.e.a': this.getAddress().substr(12)
                }
              ]
            },
            'project': { 'out.$': 1 },
            'skip': 0,
            'limit': 1
          },
          'r': {
            'f': '[ .[] | { content: .out[0].s2 } ]'
          }
        }

        let b64 = Base64.encode(JSON.stringify(query))
        let url = window.bitdbNode + b64

        let header = {
          headers: { key: window.bitdbApiKey }
        }

        axios
          .get(url, header)
          .then(response => {
            if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
              let results = []
              if (response.data.u !== undefined) {
                results = results.concat(response.data.u.reverse())
              }
              if (response.data.c !== undefined) {
                results = results.concat(response.data.c)
              }

              this.item = results[0]
              this.name = this.item.content
              this.oldName = this.name
            }
          })
          .catch(error => {
            this.errored = true
          })
      },
      getProfile () {
        this.errored = false
        let query = {
          'v': 3,
          'q': {
            'find': {
              '$and': [{
                  'out.b0': {
                    'op': 106
                  }
                }, {
  								'$or': [{
  									'out.h1': '6d05'
  								}]
  							}, {
                  'in.e.a': this.getAddress().substr(12)
                }
              ]
            },
            'project': { 'out.$': 1 },
            'skip': 0,
            'limit': 1
          },
          'r': {
            'f': '[ .[] | { content: .out[0].s2 } ]'
          }
        }

        let b64 = Base64.encode(JSON.stringify(query))
        let url = window.bitdbNode + b64

        let header = {
          headers: { key: window.bitdbApiKey }
        }

        axios
          .get(url, header)
          .then(response => {
            if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
              let results = []
              if (response.data.u !== undefined) {
                results = results.concat(response.data.u.reverse())
              }
              if (response.data.c !== undefined) {
                results = results.concat(response.data.c)
              }

              this.item = results[0]
              this.profile = this.item.content
              this.oldProfile = this.profile
            }
          })
          .catch(error => {
            this.errored = true
          })
      },
      getProfilePicture () {
        this.errored = false
        let query = {
          'v': 3,
          'q': {
            'find': {
              '$and': [{
                  'out.b0': {
                    'op': 106
                  }
                }, {
  								'$or': [{
  									'out.h1': '6d0a'
  								}]
  							}, {
                  'in.e.a': this.getAddress().substr(12)
                }
              ]
            },
            'project': { 'out.$': 1 },
            'skip': 0,
            'limit': 1
          },
          'r': {
            'f': '[ .[] | { content: .out[0].s2 } ]'
          }
        }

        let b64 = Base64.encode(JSON.stringify(query))
        let url = window.bitdbNode + b64

        let header = {
          headers: { key: window.bitdbApiKey }
        }

        axios
          .get(url, header)
          .then(response => {
            if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
              let results = []
              if (response.data.u !== undefined) {
                results = results.concat(response.data.u.reverse())
              }
              if (response.data.c !== undefined) {
                results = results.concat(response.data.c)
              }

              this.item = results[0]
              this.profilePicture = this.item.content
              this.oldProfilePicture = this.profilePicture
            }
          })
          .catch(error => {
            this.errored = true
          })
      },
      setValues () {
        let that = this
        this.updated = false
        if (this.name != this.oldName) {
          this.setName(this.name)
        }
        if (this.profile != this.oldProfile) {
          setTimeout(function(){
            that.setProfileText(that.profile)
          }, 1000)
        }
        if (this.profilePicture != this.oldProfilePicture) {
          setTimeout(function(){
            that.setProfilePicture(that.profilePicture)
          }, 2000)
        }
        if (this.profilePicture != this.oldProfilePicture) {
          setTimeout(function(){
            that.updated = true
          }, 2500)
        }
      }
    }
  };
</script>

<style>
</style>



