<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <UserNav :address="address" />
    </div>

    <div  class="s_results"  v-infinite-scroll="loadMore" infinite-scroll-disabled="scrollLoading" infinite-scroll-distance="500">
      <p class="section-title">
        Likes by
        <span v-if="usernames[address]">{{ usernames[address] }}</span>
        <span v-else>{{ address.substr(0, 12) }}...</span>
      </p>
      <div v-if="loading === false">
          <PostListItem
            v-for="(item, index) in resultBitdbItems"
            :key="index"
            :item="item">
          </PostListItem>
          <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
      </div>
      <div class="s_results" v-else>
        <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
      </div>
    </div>

    <div class="s_right topics">
    </div>

    <Footer/>
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from 'vuex'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'
import PostListItem from '@/components/Post/ListItem'
import PostNewForm from '@/components/Post/NewForm'

export default {
  name: 'UserLikes',
  mixins: [],
  props: {
    address: String
  },
  data () {
    return {
      item: null,
      offset: 0,
      page: 1,
      itemsPerPage: 20,
      resultBitsocketItems: [],
      resultBitdbItems: [],
      loading: false,
      errored: false,
      streamEnd: false,
      scrollLoading: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Post by ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address)
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
        { name: 'description', content: 'All likes by ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) + ' on Poster.cash', id: 'desc' },
        { name: 'twitter:title', content: 'Likes by ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) },
        { n: 'twitter:description', c: 'All likes by ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) + ' on Poster.cash' },
        { itemprop: 'name', content: 'Likes by ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) },
        { itemprop: 'description', content: 'All likes by ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) + ' on Poster.cash' },
        { property: 'og:title', content: 'Likes by ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer,
    UserNav,
    PostListItem,
    PostNewForm
  },
  mounted () {
    this.page = parseInt(this.offset/this.itemsPerPage) + 1
    if (this.loading === false && this.address) {
      this.searchTheBlockchainForLikes()
    }
  },
  created() {
  },
  computed: {
    ...mapState([
      'followedUsers',
      'mutedUsers',
      'myPostLikes',
      'myPostReplies',
      'profilePictures',
      'usernames',
      'likesCount',
      'repliesCount',
      'crawledAddresses',
      'crawledTxIds'
    ])
  },
  watch: {
    address: function (val) {
      if (this.loading === false) {
        this.searchTheBlockchainForLikes()
      }
    }
  },
  methods: {
    ...mapActions([
      'changeCurrentPost',
      'myLikes',
      'myReplies',
      'likesCountPerPost',
      'repliesCountPerPost',
      'addressesUsernames',
      'addressesProfilePictures'
    ]),
    searchTheBlockchainForLikes (offset = 0) {
      if (offset === 0) {
        this.loading = true
      }
      this.errored = false

      let query = {
        'v': 3,
        'q': {
          'find': {
            '$and': [{
                'out.b0': {
                  'op': 106
                }
              }, {
								'$or': [{
									'out.h1': '6d04'
								}]
							}, {
                'in.e.a': this.address
              }
            ]
          },
          'project': { 'out.$': 1 },
          'skip': parseInt(offset),
          'limit': this.itemsPerPage
        },
        'r': {
          'f': '[ .[] | { txId: .out[0].h2} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header = {
        headers: { key: window.bitdbApiKey }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let newBitdbItems = []
            if (response.data.u !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.c)
            }

            let txIds = []
            for (let i = 0; i < newBitdbItems.length; ++i) {
              txIds.push(newBitdbItems[i].txId)
            }

            if (newBitdbItems.length === 0) {
              this.streamEnd = true
            } else {
              this.searchTheBlockchainForPosts(txIds)
            }
          }
          this.offset += this.itemsPerPage
          this.loading = false
          this.scrollLoading = false;
        })
        .catch(error => {
          this.errored = true
          this.loading = false
        })
    },
    searchTheBlockchainForPosts (txIds) {
      this.errored = false

      let followedSearch = {}
      followedSearch['$or'] = []
      for (let i = 0; i < txIds.length; ++i) {
        followedSearch['$or'].push({ 'tx.h': reverseHex(txIds[i]) })
      }

      let query = {
        'v': 3,
        'q': {
          'find': {
            '$and': [{
                'out.b0': {
                  'op': 106
                }
              }, {
								'$or': [{
									'out.h1': '6d02'
								}, {
									'out.h1': '6d03'
								}, {
									'out.h1': '6d0c'
								}
              ]},
              followedSearch
            ]
          },
          'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 }
        },
        'r': {
          'f': '[ .[] | {prefix: .out[0].h1, sender: .in[0].e.a, parentTxId: .out[0].h2, content1: .out[0].s2, content2: .out[0].s3, timestamp: .blk.t?, txId: .tx.h} ]'
        }
      }

//      console.log(JSON.stringify(query))

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header = {
        headers: { key: window.bitdbApiKey }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let newBitdbItems = []
            if (response.data.u !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.c)
            }

            // populate usernames and txIds
            let newAddresses = []
            if (this.getAddress()) {
              newAddresses.push(this.getAddress().substr(12))
            }
            let newTxIds = []
            for (let i = 0; i < newBitdbItems.length; ++i) {
              if(newAddresses.indexOf(newBitdbItems[i].sender) === -1) {
                newAddresses.push(newBitdbItems[i].sender)
              }
              if(newTxIds.indexOf(newBitdbItems[i].txId) === -1) {
                newTxIds.push(newBitdbItems[i].txId)
              }
            }
            let that = this
            newAddresses = newAddresses.filter( function( el ) {
              return that.crawledAddresses.indexOf( el ) < 0;
            } )
            newTxIds = newTxIds.filter( function( el ) {
              return that.crawledTxIds.indexOf( el ) < 0;
            } )

            this.myLikes([ this.getAddress(), newTxIds ])
            this.myReplies([ this.getAddress(), newTxIds ])
            this.likesCountPerPost([ newTxIds ])
            this.repliesCountPerPost([ newTxIds ])
            this.addressesUsernames([ newAddresses ])
            this.addressesProfilePictures([ newAddresses ])
            this.resultBitdbItems = this.resultBitdbItems.concat(newBitdbItems)

            if (newBitdbItems.length === 0) {
              this.streamEnd = true
            }
          }
          this.offset += this.itemsPerPage
          this.loading = false
          this.scrollLoading = false;
        })
        .catch(error => {
          this.errored = true
          this.loading = false
        })
    },
    loadMore() {
      this.scrollLoading = true;
      if (this.streamEnd === false && this.errored === false && this.loading === false) {
        this.searchTheBlockchainForLikes(this.offset)
      }
    }
  }
}
</script>

<style>
</style>



