<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <UserNav :address="address" />
    </div>

    <div  class="s_results"  v-infinite-scroll="loadMore" infinite-scroll-disabled="scrollLoading" infinite-scroll-distance="500">
      <p class="section-title">
        Followers of
        <span v-if="usernames[address]">{{ usernames[address] }}</span>
        <span v-else>{{ address.substr(0, 12) }}...</span>
      </p>
      <span v-if="loading === false">
        <span  v-if="followers.length > 0">
          <div class="stream-item" v-for="(item, index) in followers">
              <div class="stream-item-header">
                <router-link :to="'/user/' + item" class="account-group">
                  <img class="avatar" v-if="profilePictures[item]" :src="profilePictures[item]" alt="">
                  <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
                    <span class="full-name-group">
                      <strong class="full-name text-truncate" v-if="usernames[item]">{{ usernames[item] }}</strong>
                      <strong class="full-name text-truncate" v-else>{{ item.substr(0, 12) }}...</strong>
                      <span class="user-badges">
                          <strong class="full-name text-truncate" v-if="followedUsers.includes(item)"><i class="fas fa-star following"></i></strong>
                      </span>
                    </span>
                </router-link>
              </div>
          </div>
        </span>
        <span v-else>
          <div class="n_results">No followers</div>
        </span>
      </span>
      <span v-else>
        <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
      </span>
    </div>

    <div class="s_right topics">
    </div>

    <Footer/>
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from 'vuex'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'

export default {
  name: 'UserFollowers',
  mixins: [],
  props: {
    address: String
  },
  data () {
    return {
      unfollowers: {},
      followers: [],
      offset: 0,
      page: 1,
      itemsPerPage: 24,
      loading: false,
      errored: false,
      streamEnd: false,
      scrollLoading: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Followers of ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address)
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
        { name: 'description', content: 'All users following ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) + ' on Poster.cash', id: 'desc' },
        { name: 'twitter:title', content: 'Followers of ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) },
        { n: 'twitter:description', c: 'All users following ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) + ' on Poster.cash'},
        { itemprop: 'name', content: 'Followers of ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) },
        { itemprop: 'description', content: 'All users following ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) + ' on Poster.cash' },
        { property: 'og:title', content: 'Followers of ' + (this.usernames[this.address] ? this.usernames[this.address] : this.address) },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer,
    UserNav
  },
  mounted () {
    this.getUnfollowers(this.address)
  },
  created() {
  },
  computed: {
    ...mapState([
      'crawledAddresses',
      'followedUsers',
      'profilePictures',
      'usernames'
    ])
  },
  watch: {
    followers: function (val) {
      let that = this
      let newAddresses = val.filter( function( el ) {
        return that.crawledAddresses.indexOf( el ) < 0;
      } )
      if (this.getAddress()) {
        newAddresses.push(this.getAddress().substr(12))
      }
      this.addressesUsernames([ newAddresses ])
      this.addressesProfilePictures([ newAddresses ])
    }
  },
  methods: {
    ...mapActions([
      'addressesUsernames',
      'addressesProfilePictures'
    ]),
    getUnfollowers (userAddress) {
      this.loading = true
      this.errored = false

      const bytes = bs58.decode(bchaddr.toLegacyAddress(userAddress))
      const userAddressHex = bytes.toString('hex').substr(2,40)

      let findQuery = {}
      findQuery['$and'] = []
      findQuery['$and'].push({'out.b0':{ 'op': 106 }})
      let findPrefixes = {}
      findPrefixes['$or'] = []
      findPrefixes['$or'].push({'out.h1':'6d07'})
      findQuery['$and'].push(findPrefixes)
      findQuery['$and'].push({'out.h2': ''  + userAddressHex + ''})

      let query = {
        'v': 3,
        'q': {
          'find': findQuery,
          'project': { 'in': 1, 'out.$': 1, 'blk': 1 },
          'limit': 100
        },
        'r': {
          'f': '[ .[] | {address: .in[0].e.a, timestamp: .blk.t?} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header = {
        headers: { key: window.bitdbApiKey }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let result_a = []
            if (response.data.u !== undefined) {
              result_a = result_a.concat(response.data.u)
            }
            if (response.data.c !== undefined) {
              result_a = result_a.concat(response.data.c)
            }
            let result = result_a.reduce(function(acc, curr) {
              if (curr._id) {
                var fromMap = acc.map[curr._id];
                if (!fromMap) {
                  acc.map[curr._id] = fromMap = {
                    count: 0,
                    _id: curr._id
                  }
                  acc.result.push(fromMap);
                }
                fromMap.count += parseFloat(curr.count);
              } else {
                acc.result.push(curr);
              }
              return acc;
            }, {
              map: {},
              result: []
            }).result

            let unfollowResult = {}
            for (let i = 0; i < result.length; ++i) {
              if (unfollowResult[result[i].address] === undefined) {
                unfollowResult[result[i].address] = parseInt(result[i].timestamp)
              }
            }
            this.unfollowers = unfollowResult
            this.getFollowers(this.address)
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error))
          this.errored = true
          this.loading = false
        })
    },
    getFollowers (userAddress, offset = 0) {
      if (offset === 0) {
        this.loading = true
      }
      this.errored = false

      const bytes = bs58.decode(bchaddr.toLegacyAddress(userAddress))
      const userAddressHex = bytes.toString('hex').substr(2,40)

      let findQuery = {}
      findQuery['$and'] = []
      findQuery['$and'].push({'out.b0':{ 'op': 106 }})
      let findPrefixes = {}
      findPrefixes['$or'] = []
      findPrefixes['$or'].push({'out.h1':'6d06'})
      findQuery['$and'].push(findPrefixes)
      findQuery['$and'].push({'out.h2': ''  + userAddressHex + ''})

      let query = {
        'v': 3,
        'q': {
          'find': findQuery,
          'project': { 'in': 1, 'out.$': 1, 'blk': 1 },
          'skip': parseInt(offset),
          'limit': this.itemsPerPage
        },
        'r': {
          'f': '[ .[] | {address: .in[0].e.a, timestamp: .blk.t?} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header = {
        headers: { key: window.bitdbApiKey }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let result_a = []
            if (response.data.u !== undefined) {
              result_a = result_a.concat(response.data.u)
            }
            if (response.data.c !== undefined) {
              result_a = result_a.concat(response.data.c)
            }
            let result = result_a.reduce(function(acc, curr) {
              if (curr._id) {
                var fromMap = acc.map[curr._id];
                if (!fromMap) {
                  acc.map[curr._id] = fromMap = {
                    count: 0,
                    _id: curr._id
                  }
                  acc.result.push(fromMap);
                }
                fromMap.count += parseFloat(curr.count);
              } else {
                acc.result.push(curr);
              }
              return acc;
            }, {
              map: {},
              result: []
            }).result

            let followResult = {}
            for (let i = 0; i < result.length; ++i) {
              if (followResult[result[i].address] === undefined) {
                followResult[result[i].address] = result[i].timestamp
              }
            }
            let followers = []
            for(let address in followResult) {
              if ((this.unfollowers[address] === undefined || followResult[address] > this.unfollowers[address]) && !(this.followers.includes(address))) {
                followers.push(address)
              }
            }
            if (result.length === 0) {
              this.streamEnd = true
            }
            this.followers = this.followers.concat(followers)
            this.offset += this.itemsPerPage
            this.loading = false
            this.scrollLoading = false
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error))
          this.errored = true
          this.loading = false
        })
    },
    loadMore() {
      this.scrollLoading = true;
      if (this.streamEnd === false && this.errored === false && this.loading === false) {
        this.getFollowers(this.address, this.offset)
      }
    }
  }
}
</script>

<style>
</style>



